<template>
  <DataMatchProcess />
</template>

<script>
import { defineComponent, watch } from 'vue'
import { useLogin } from 'composable/user'
import { useRouter } from 'vue-router'
import DataMatchProcess from 'components/DataMatchProcess.vue'

export default defineComponent({
  components: {
    DataMatchProcess
  },
  setup() {
    const { datamatchStatus } = useLogin()
    const router = useRouter()

    if (datamatchStatus.value == 0) {
      router.push({ name: 'invoice' })
    }

    watch(
      () => datamatchStatus.value,
      () => {
        if (datamatchStatus.value == 0) {
          router.push({ name: 'invoice' })
        }
      }
    )

    return { datamatchStatus }
  }
})
</script>